body{
    scrollbar-width: thin;
    scrollbar-color: darkorange rgb(145, 91, 12);
}
::-webkit-scrollbar {
  width: 3px;
}
/* Track */
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px rgb(0, 0, 0); 
  border-radius: 10px;
}
 
/* Handle */
::-webkit-scrollbar-thumb {
  background: darkorange; 
  border-radius: 10px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: red;
}
.docs-main{
    display: grid;
    grid-template-columns: 1fr 2fr 4px 5fr 1fr;
    grid-template-rows: 1fr;
    grid-template-areas: ". sol . orta .";
    color: var(--color);
    .sol{
        box-sizing: border-box;
        grid-area: sol;
        background-color: #12c0ff;
        height: 85vh;
        padding: 16px 0;
        position: relative;
        .sol-icerik{
            height: 100%;
            margin: 0;
            padding: 0 16px;
            overflow-y: scroll;
            overflow-x: hidden ;
            z-index: 1;
        }
        .sol-menu-closed{
            position: absolute;
            top: 0;
            left: 0;
            bottom: 0;
            right: 0;
            background-color: #12c0ff;
            z-index: 2;
            display: flex;
            justify-content: center;
            align-items: center;
            -webkit-touch-callout: none;
            -webkit-user-select: none;
            -khtml-user-select: none; 
            -moz-user-select: none; 
            -ms-user-select: none;
            user-select: none;
            .sol-menu-toggle-icon{
                transform: scaleY(3);
            }
            .toggle-hamburger{
                position: absolute;
                top: 16px;
            }
        }
        .sol-menu-opened{
            position: absolute;
            top: 0;
            bottom: 0;
            right: -40px;
            width: 60px;
            background-color: #12c0ff;
            z-index: 2;
            display: flex;
            justify-content: center;
            align-items: center;
            -webkit-touch-callout: none;
            -webkit-user-select: none;
            -khtml-user-select: none; 
            -moz-user-select: none; 
            -ms-user-select: none;
            user-select: none;
            .sol-menu-toggle-icon{
                transform: scaleY(3);
            }
            .toggle-hamburger{
                position: absolute;
                top: 16px;
            }
        }
    }
    .orta{
        box-sizing: border-box;
        grid-area: orta;
        background-color: darkorange;
        min-height: 100vh;
        padding: 16px;
    }
}
