}

@media only screen and (max-width:768px){
    .docs-main{
        grid-template-columns: 1fr 50px 4px 20fr 1fr;
        .sol{
            .sol-icerik{
                height: 95%;
                width: auto;
                overflow-y: hidden;
                overflow-x: hidden ;
            }
        }
    }
    
}
@media only screen and (min-width:768px) and (max-width:992px){
    .docs-main{
        grid-template-columns: 1fr 3fr 4px 6fr 1fr;
        .sol{
            .sol-icerik{
                height: 95%;
                width: auto;
                overflow-y: scroll;
                overflow-x: hidden ;
            }
        }
    }
}